<template>
  <div>
    <div class="container-fluid">
      <div class="row justify-content-center d-lg-none d-md-none p-3">
        <div class="pagination justify-content-center">
          <el-pagination
            small
            background
            :current-page="currentPage"
            layout="prev, pager, next"
            :total="count">
          </el-pagination>
        </div>
      </div>
      <div class="row  d-none d-md-block d-lg-block p-3">
        <div class="pagination justify-content-center">
          <el-pagination
            background
            :current-page="currentPage"
            layout="prev, pager, next"
            :total="count">
          </el-pagination>
        </div>
      </div>
      <div class="row p-0 m-auto">
        <passage-card class="clo-sm-12 col-md-4 col-lg-3 mb-3" v-for="item in passages" :passage="item" :key="item.objectId"></passage-card>
      </div>
    </div>
  </div>
</template>

<script>
import PassageItem from "./PassageItem";
import PassageCard from "./PassageCard";

export default {
  name: "PassageManager",
  components: {PassageCard, PassageItem},
  data() {
    return {
      count:0,
      currentPage: 1,
      passages: []
    }
  },
  mounted() {
    this.getPassageList()
  },
  methods: {
    getPassageCount(){
      const query = Bmob.Query('PassageList');
      query.count().then(res => {
        this.count = res
      });
    },
    handleCurrentChange(val) {
      this.cur_page = val;
    },
    handleSizeChange(val) {
      this.pageNum = val;
    },
    totalPageNum() {

    },

    getPassageList() {
      const query = Bmob.Query("PassageList");
      query.limit(10);
      query.skip((this.currentPage-1)*10)
      query.order("-createdAt");
      query.find().then(res => {
        this.passages = res
      });
    }
  }
}
</script>

<style scoped>
.text {
  font-size: 0.9em;
}

.box-card {
  width: 480px;
}
</style>



